* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

main {
  background-color: gray;
  display: flex;
}

a {
  color: #40779a;
  text-decoration: none;
}

#menu {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

li {
  list-style: none;
}

h1, h3, p {
  color: #f5f5f5;
}

header {
  background-color: #fc0;
  justify-content: center;
  display: flex;
}

#search-bar {
  width: 20rem;
  height: 2rem;
}

#submit {
  width: 10ch;
  height: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#nav-search-form {
  position: relative;
}

#live-search-dropdown {
  background-color: #0ff;
  width: 20rem;
  height: 20rem;
  display: none;
  position: absolute;
}

#movie-section {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

#bookmarks {
  background-color: green;
  width: 20rem;
  height: 95vh;
  padding: 10px;
}

.movie-container {
  background-color: #161616;
  border: 1.5px solid #000;
  flex-direction: column;
  gap: 10px;
  width: 60%;
  display: flex;
}

.poster {
  height: 300px;
}

.bookmark-button {
  cursor: pointer;
  background-color: #fc0;
  border: none;
  width: 10rem;
  padding: 10px;
  font-size: 16px;
}

.bookmark-button:hover {
  background-color: #e6b800;
}
